.goals {}

.goals .all-goals-box {
    margin-top: 60px;
    padding: 50px 0;
    /* background: url('../../images/5881775159038296687.jpg');
    background-repeat: no-repeat;
    background-size: cover; */

}

.goals .all-goals-box .goals-box {
    width: calc(100% / 3 - 100px);
    height: 230px;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 4px solid white;
    background-color: #8080806b;
}

