.goals .all-goals-box .goals-box .title {}

.goals .all-goals-box .goals-box .title h1 {
    font-size: 36px;
    text-align: center;
    color: #2f2f2f;
    text-transform: capitalize;
}

.goals .all-goals-box .goals-box .description {
    text-align: center;
    width: 90%;
    margin: 38px auto;
}

.goals .all-goals-box .goals-box .description p {
    color: #1e1e1e;
    font-weight: 500;
    font-size: 14px;
}

@media (max-width:768px) {
    .goals .all-goals-box .goals-box {
        width: calc(100% / 2 - 100px);
    }
}

@media (max-width:556px) {
    .goals .all-goals-box .goals-box {
        width: calc(100% / 1 - 100px);
        height: 200px;
    }

    .goals .all-goals-box .goals-box .title h1 {
        font-size: 26px;
    }

    .goals .all-goals-box .goals-box .description {

        margin: 20px auto;
    }
}